:root {
    font-size: 24px;
}

body{
    display: flex;
    flex-direction: column;
    background-color: $fifthColor;
    font-family: "quantico", sans-serif;
    line-height: 1;
    margin: auto;
    font-size: $primaryFontSize;
}

h1, h2, h3{
    color: $primaryColor;
}

h4{
    color: $tertiaryColor;
}

h1{
    font-size: $sixthFontSize;
}

h2{
    font-size: $fifthFontSize;
}

.logo{
    max-width: 75px;
    height: auto;
}

.intro{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    line-height: 2.5rem;
    padding: 2rem 2rem 2rem 2rem;
    position: relative;
    margin-bottom: 2rem;

    &_p{
        color: $secondaryColor;
        font-size: $tertiaryFontSize;
    }

    &_background{
        position: absolute;
        z-index: -1;
        transform: rotate(15deg);
        font-size: 180px;
        color: white;
        opacity: 0.02;
        top: 100px;
        right: 0;
        bottom: 0;
        left: 120px;
    }

    & h1{
        display: flex;
        flex-direction: column;
        margin-bottom: 1rem;
    }
    & button{
        align-self: flex-start;
    }
}

.aboutme{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 2rem;
    padding: 0rem 2rem 0rem 2rem;

    &_portrait{
        display: flex;
        position: relative;
        max-width: 100%;
        height: auto;
        margin-bottom: 2rem;

        &_img{ 
            max-width: 500px;
            height: auto;
            position: relative;
            z-index: 0;
            margin-right: 2rem;
        }

        &_frame{
            position: absolute;
            z-index: -1;
            width: 450px;
            height: 500px;
            border: 10px solid $seventhColor;
            border-radius: 10px;
            top: 25px;
            right: 0;
            bottom: 0;
            left: 75px;
        }
    }

    &_description{
        margin-left: 3rem;

        &_intro{

            & h2{
                margin: 0;
            }

            & p{
                display: flex;
                flex-direction: column;
                color: $secondaryColor;

                & span{
                    padding-top: 1rem;
                }

            }
        }

        &_areaBtn{
            display: flex;
            justify-content: flex-start;
            align-items: center;
            
            & button{
                margin-right: 1rem;
            }
        }
    }
}

.portfolio{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0rem 2rem 0rem 2rem;

    &_menu{
        margin-bottom: 0;
    }

    &_project{
        display: flex;
        justify-content: space-evenly;
        margin-bottom: 3rem;

        &_img{
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            margin-left: 2rem;

            & img{
                position: relative;
                max-width: 600px;
                height: auto;
            }

            & div{
                position: absolute;
                z-index: -1;
                width: 500px;
                height: 500px;
                border: 10px solid $seventhColor;
                border-radius: 10px;
                top: 20px;
                right: 0;
                bottom: 0;
                left: -40px;
            }
        }

        &_description{
            width: 50%;
            color: $primaryColor;

            &_p{
                color: $secondaryColor;
            }

            &_number{
                color: $tertiaryColor;
            }

            &_link{
                display: flex;
                flex-direction: row;
                align-items: center;

                &_arrow{
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    $border: 10px;
                    
                    &_stick{
                        border: 2px solid $primaryColor;
                        border-radius: 10px;
                        width: 40px;
                        height: 0;
                    }

                    &_end{
                        display: flex;
                        align-items: center;
                        margin-right: 1rem;
                        height: 0;
                        width: 0;
                        border-top: $border solid transparent; /* Hauteur de la flèche */
                        border-bottom: $border solid transparent;
                        border-left: $border solid $primaryColor; /* Couleur de la flèche */
                    }
                }
            }
        }

        &_img{
            height: 100%;

            & img{
                width: auto;
                max-height: 500px ;
            }
        }
    }

    & button{
        margin-bottom: 2rem;
    }
}

.formations{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 3rem;
    padding: 0rem 2rem 0rem 2rem;

    & h2{
        margin-bottom: 3rem;
    }

    &_list{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 3rem;

        &_frame{
            border-radius: 15px;

            &_header{
                display: flex;
                flex-direction: row;
                align-items: center;
                position: relative;

                & img{
                    width: auto;
                    max-height: 80px;
                    margin-right: 1rem;
                }

                &_title{
                    font-weight: bold;
                }
            }

            &_skills{

            }
        }
    }
}

.technologies{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0rem 2rem 0rem 2rem;
    
    &_div{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        margin-top: 3rem;

        &_list{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin: 0rem 5rem 2rem 5rem;

            & img{
                max-width: 200px;
                max-height: auto;
            }

            & p{
                color: $secondaryColor;
                font-size: $tertiaryFontSize;
                font-weight: bold;
            }
        }
    }
}

.xp{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0rem 2rem 0rem 2rem;

    & h2{
        margin-bottom: 3rem;
    }

    &_list{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        color: $primaryColor;

        &_title{
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: linear-gradient(to right, #56ADD5, #740CDC);
            border-radius: 15px;
            padding: 0rem 1rem 0rem 1rem;
            margin-bottom: 1rem;
            gap: 60px;
            width: 75%;
            height: auto;
        }

        &_description{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            background: $seventhColor;
            border-radius: 15px;
            padding: 1rem;
            margin-bottom: 2rem;
            width: 75%;
            height: auto;

            &_text{
                margin-bottom: 1rem;

            }

            &_contact{
                display: flex;
                justify-content: flex-start;
                align-items: center;
                gap: 70px;

                &_adress{
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    gap: 10px;
                    color: $eightColor;
                }

                &_email{
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    gap: 10px;

                    & a{
                        text-decoration: none;
                        color: $eightColor;
                    }
                }

                & img{
                    max-width: 20px;
                    height: 100%;
                }
            }

            &_skills{
                display: flex;
                flex-wrap: wrap;
                justify-content: flex-start;
                align-items: center;

                & p{
                    background: $sixthColor;
                    padding: 0.5rem;
                    border-radius: 15px;
                    margin: 0rem 1rem 0.5rem 0rem;
                    text-align: center;
                }
            }
        }
    }
}

.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 200px 200px 0px 0px;
    margin-top: 2rem;
    background: linear-gradient(to bottom right, #740CDC, #56ADD5);
    padding: 0rem 2rem 1rem 2rem;

    & h2{
        margin-bottom: 2rem;
    }

    &_contact{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: auto;
        margin-bottom: 1rem;

        &_informations{
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            margin-right: 5%;

            & h3{
                margin: 0rem 0rem 1rem 0rem;
            }

            &_description{
                color: $ninethColor;
                max-width: 700px;
                margin: 0rem 0rem 1rem 0rem;
            }

            &_data{
                display: flex;
                justify-content: flex-start;
                align-items: center;
                color: $primaryColor;
                margin-bottom: 1rem;

                & img{
                    max-width: 50px;
                    height: auto;
                    margin-right: 1rem;
                }

                & a{
                    color: $primaryColor;
                    text-decoration: none;
                }
            }
        }

        &_form{
            display: flex;
            flex-direction: column;
            padding: 1rem;
            width: 60%;
            border-radius: 15px;
            background: linear-gradient(to top left, #740CDC, #56ADD5);


            & input{
                background: $fifthColor;
                font-size: 15px;
                border: none;
                margin-bottom: 0.5rem;
                border-radius: 15px;
                padding: 0.5rem;
                color: $primaryColor;

                & ::placeholder{
                    font-size: $primaryFontSize;
                    font-weight: bold;
                }
            }

            & textarea{
                background: $fifthColor;
                border: none;
                padding: 0.5rem;
                margin-bottom: 0.5rem;
                border-radius: 15px;
                color: $primaryColor;
                resize: none;

                & ::placeholder{
                    font-size: $primaryFontSize;
                    font-weight: bold;
                }
            }

            &_sender{
                color: $primaryColor;
                font-weight: bold;
                width: 150px;
            }
        }
    }

    &_line{
        border: 1px solid $primaryColor;
        width: 100%;
        height: auto;
        margin: 1rem 0rem 1rem 0rem;
    }

    &_mentions{
        display: flex;
        width: 100%;
        height: auto;
        justify-content: space-around;
        align-items: center;
        color: $primaryColor;
        font-size: 20px;

        &_socialMedia{
            display: flex;

            & img{
                width: 60px;
                height: auto;
                margin: 0rem 0.5rem 0rem 0.5rem;
            }
        }
        
    }
}